t .el-input {\n    width: 142px;\n}\n.disciplineEvaluation .el-pagination .el-select .el-input input {\n    height: 40px;\n    font-size: 20px;\n}\n.disciplineEvaluation .el-pagination .el-select {\n    vertical-align: top;\n}\n.disciplineEvaluation .el-pagination button, .disciplineEvaluation .el-pagination span {\n    height: 40px;\n    line-height: 40px;\n    font-size: 20px;\n}\n.disciplineEvaluation .el-pagination .el-pagination__editor {\n    height: 40px;\n    min-width: 40px;\n    font-size: 20px;\n    vertical-align: top;\n}\n"],"sourceRoot":""}]);

